import React, { Component } from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components';
import StarRatingComponent from 'react-star-rating-component';

const Line = styled.div`
  color: azure;
  float: right;
  width: 340px;
`;

class Clock extends React.Component {
    constructor(props) {
      super(props);
      this.state = {date0: new Date(),
                    date1: new Date()};
    }
    componentDidMount() {
      this.timerID = setInterval(
        () => this.tick(),
        1000
      );
    }
    componentWillUnmount() {
      clearInterval(this.timerID);
    }
    tick() {
      var d = new Date();
      var hours = (this.props.info[5]-this.props.info[5]%60)/60
      var min = this.props.info[5]%60
      d.setHours(d.getHours() + hours);
      d.setMinutes(d.getMinutes() + min);
      this.setState({
        date0: new Date(),
        date1: d
      });
    }
    render() {
      const { info } = this.props
      return (

        <Line>
          {this.state.date0.toLocaleTimeString()}
          {'___'+(info[5]-info[5]%60)/60+':'+info[5]%60+'___'}
          {this.state.date1.toLocaleTimeString()}
        
          <StarRatingComponent 
           name="rate" 
           className={"Rating"}
           starCount={9}
           value={info[3]}
           starColor={'#3b00ff'}
           emptyStarColor={'#34ada4'}
        />
          
            <img 
              src={'http://imdb.snick.ru/ratefor/01/' + info[8] + '.png'}
            >
            </img>
          
        </Line>
      )
    }
  }
  const mapStateToProps = (state) => ({
	info: state.post.info
  })
  export default connect(mapStateToProps)(Clock)